import React from 'react';
import ReactDOM from 'react-dom';

import html2canvas from 'html2canvas'
import defaultIcon from '../../../public/images/icon/default-icon-mypage.png';

(() => {

  let gameUserJson = JSON.parse(document.getElementById('gameUserJson').value);
  let rankJson = JSON.parse(document.getElementById('rankJson').value);

  const gameUserChecks = () => {

    let checks = [];
    gameUserJson.game_user_checks.forEach((gameUserCheck, index) => {
      checks[gameUserCheck.item_id] = true;
    });

    return checks;
  }

  // html2canvas  URI 
  // Ref: https://stackoverflow.com/questions/31656689/how-to-save-img-to-users-local-computer-using-html2canvas
  const saveAsImage = uri => {
    const downloadLink = document.createElement("a");
    if (typeof downloadLink.download === "string") {
      downloadLink.href = uri;
      // 
      downloadLink.download = "component.png";
      // Firefox  body 
      document.body.appendChild(downloadLink);
      //  a 
      downloadLink.click();
      // Firefox 
      document.body.removeChild(downloadLink);
    } else {
      window.open(uri);
    }
  }

  const onClickExport = () => {
    //  component  id 
    const target = document.getElementById("resume");
    html2canvas(target).then(canvas => {
      const targetImgUri = canvas.toDataURL("img/jpg");
      saveAsImage(targetImgUri);
    });
  }

  const HeaderComponent = () => {

    return(
      <div className="col-12 page-header">
        <div className="header-font">
          
        </div>
        <div className="ml-auto">
          <button
            className="btn site-color text-white rounded-pill btn-outline-secondary text-center"
            onClick={() => onClickExport()}>
            
          </button>
          <button className="btn site-color text-white rounded-pill btn-outline-secondary text-center"
                  onClick={() => {location.href='/user/' + gameUserJson.user.id + '/edit'}}>
            
          </button>
        </div>
      </div>
    );
  }

  const ResumeComponent = () => {

    return(
      <div id="resume" className="resume">
        <div className="content">

          <div className="header">
            <div className="title"></div>
          </div>

          <div className="profile row">
            <div className="col-5">
              <img
                className="profileImage"
                src={"/storage/images/temp/twitter_game_3_user_" + gameUserJson.user.id + ".jpg"}
              />
            </div>
            <div className="col-7">
              <div className="row">
                <div className="name col-12">
                  <div className="title"></div>
                  <div className="body">
                    {gameUserJson.user.name}
                  </div>
                </div>
              </div>

              <div className="row">
                <div className="rate col-8">
                  <div className="title"></div>
                  <div className="body">
                    <span className="number">{rankJson.ranking}</span>
                    <span className="staring"></span>

                  </div>
                </div>
                <div className="gender col-4">
                  <div className="title"></div>
                  <div className="body">
                    {gameUserJson.user.gender === 1 ? ''
                      : gameUserJson.user.gender === 2 ? '' : ''
                    }
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="like row">
            <div className="col-6">
              <div className="experience">
                <div className="title"></div>
                <div className="body">
                  {gameUserJson.experience}
                </div>
              </div>
            </div>
            <div className="col-6">
              <div className="area">
                <div className="title"></div>
                <div className="body">
                  {gameUserJson.area}
                </div>
              </div>
            </div>
          </div>

          <div className="like row">

            <div className="col-6">
              <div className="pokemon">
                <div className="title"></div>
                <div className="body">
                  {gameUserJson.preference}
                </div>
              </div>
            </div>

            <div className="col-6">
              <div className="regulation">
                <div className="title"></div>
                <div className="body">
                  <div className="row">
                    {gameUserChecks()[1]
                      ? <div className='standard'> </div>
                      : <div className='standard fontGrey'> </div>
                    }
                    {gameUserChecks()[2]
                      ? <div className='legend'> </div>
                      : <div className='legend fontGrey'> </div>
                    }
                  </div>
                  <div className="row">
                    {gameUserChecks()[3]
                      ? <div className='extra'> </div>
                      : <div className='extra fontGrey'> </div>
                    }
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="playStyle row">
            <div className="title"></div>
            <div className="body">
              <div className="row">
                <div className="col-4">
                  {gameUserChecks()[101]
                    ? <div className='left'> </div>
                    : <div className='left fontGrey'> </div>
                  }
                </div>
                <div className="col-4">
                  {gameUserChecks()[102]
                    ? <div className='center'> </div>
                    : <div className='center fontGrey'> </div>
                  }
                </div>
                <div className="col-4">
                  {gameUserChecks()[103]
                    ? <div className='right'> </div>
                    : <div className='right fontGrey'> </div>
                  }
                </div>
              </div>
              <div className="row">
                <div className="col-4">
                  {gameUserChecks()[104]
                    ? <div className='left'> </div>
                    : <div className='left fontGrey'> </div>
                  }
                </div>
                <div className="col-4">
                  {gameUserChecks()[105]
                    ? <div className='center'> </div>
                    : <div className='center fontGrey'> </div>
                  }
                </div>
                <div className="col-4">
                  {gameUserChecks()[106]
                    ? <div className='right'> </div>
                    : <div className='right fontGrey'> </div>
                  }
                </div>
              </div>
            </div>
          </div>

          <div className="freeSpace row">
            <div className="title"></div>
            <div className="body">
              {gameUserJson.user.body}
            </div>
          </div>

        </div>
      </div>
    );
  }


  const Component = () =>
    <div>
      <div className="d-flex flex-row align-items-center p-2">
        <HeaderComponent/>
      </div>
      <div className="d-flex flex-row align-items-center p-2">
        <ResumeComponent/>
      </div>
    </div>

  ReactDOM.render(
    <Component/>,
    document.getElementById('target-component')
  );

})();
